figure.Vid {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	.inner {
		width: 1180px;
		margin: 0 auto;

		&::after {
			overflow: hidden;
		}
	}
	article {
		float: left;
		width: 280px;
		height: 280px;
		margin-right: 20px;
		cursor: pointer;

		&:last-child {
			margin-right: 0;
		}

		.pic {
			width: 100%;
			height: 100%;
			overflow: hidden;
			border-radius: 100%;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				transform: scale(1.4);
				filter: grayscale(1);
				transition: 0.5s;
			}
		}

		&:hover .pic img {
			filter: none;
		}
	}
}
